@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@500;600&family=Outfit:wght@300;400;600&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Barlow Semi Condensed", sans-serif;
  font-size: 13px;
  background-color: hsl(210, 46%, 95%);
}

.contenedor-general {
  padding: 30px 0px 0px 30px;
}

.contenedor-card > div:nth-child(n) {
  border-radius: 10px;
  margin: 0px 30px 30px 0px;
  padding: 30px;
}

h3,
p {
  margin: 0px;
}

.contenedor-card {
  display: inline-grid;
  grid-template-columns: repeat(4, 276px);
  padding: calc(50vh - 310px) calc(50% - 552px);
}

.card1 {
  grid-column: 1/3;
  grid-row: 1/2;
}

.card2 {
  grid-column: 3/4;
  grid-row: 1/2;
}

.card3 {
  grid-column: 4/5;
  grid-row: 1/3;
}

.card4 {
  grid-column: 1/2;
  grid-row: 2/3;
}

.card5 {
  grid-column: 2/4;
  grid-row: 2/3;
}

@media only screen and (max-width: 599px) {
  .contenedor-card {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    padding: calc(50vh - 310px) calc(50% - 552px);
  }

  .card1 {
    grid-column: 1/2;
    grid-row: 1/2;
  }

  .card2 {
    grid-column: 1/2;
    grid-row: 2/3;
  }

  .card3 {
    grid-column: 1/2;
    grid-row: 3/4;
  }

  .card4 {
    grid-column: 1/2;
    grid-row: 4/5;
  }

  .card5 {
    grid-column: 1/2;
    grid-row: 5/6;
  }
}

@media only screen and (min-width: 600px) and (max-width: 1000px) {
  .contenedor-card {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: calc(50vh - 310px) calc(50% - 552px);
  }

  .card1 {
    grid-column: 1/3;
    grid-row: 1/2;
  }

  .card2 {
    grid-column: 3/4;
    grid-row: 1/2;
  }

  .card3 {
    grid-column: 1/4;
    grid-row: 2/3;
  }

  .card4 {
    grid-column: 1/2;
    grid-row: 3/4;
  }

  .card5 {
    grid-column: 2/4;
    grid-row: 3/4;
  }
}

.card-titulo {
  display: flex;
  justify-content: space-between;
}

.card-informacion {
  display: flex;
  align-items: center;
  margin: 0px;
  position: relative;
  z-index: 1;
}

.card-titulo img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 18px;
}

.card-autor-oficio {
  opacity: 0.5;
}

.card-contenido {
  opacity: 0.7;
}

.pattern img {
  position: absolute;
  border-radius: 0px;
  width: 110px;
  height: 110px;
  margin-left: -110px;
  margin-top: -30px;
}

.texto-titulo {
  position: relative;
  z-index: 1;
}

